*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Roboto Mono", monospace;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #f9fafb;
}

.contenedor {
  width: 92%;
  max-width: 950px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
}

.heading {
  display: flex;
  align-items: center;
  justify-content: center;
}
.heading__text {
  font-size: 4rem;
  margin-bottom: 5rem;
  color: #0f172a;
  position: relative;
  background-image: linear-gradient(351deg, #60a5fa 23%, #2563eb 87%, #1e40af 102%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.heading__text::after {
  content: "";
  position: absolute;
  width: 15rem;
  height: 0.5rem;
  background-color: #334155;
  inset: 0;
  margin: 5rem auto;
  border-radius: 1px;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

p {
  font-size: 1.6rem;
  color: #4b5563;
  margin: 0;
}

/** Media Queries **/
.boton {
  border: 2px solid transparent;
}

.header__desktop {
  display: none;
}
@media (min-width: 580px) {
  .header__desktop {
    display: initial;
    background-color: #f3f5f7;
    border-bottom: 1px solid #cbd5e1;
    height: 6.5rem;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
  }
}
@media (min-width: 580px) {
  .header__mobile {
    display: none;
  }
}
.header__top {
  height: 5rem;
  background-color: #f3f5f7;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid #6b7280;
  z-index: 10;
}
.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header__logo {
  width: 8.5rem;
}
.header__imagen--dark {
  display: none;
}

/** MOBILE STYLES NAVEGATION **/
.mobile {
  height: 5.8rem;
  background-color: #f9fafb;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: 1px solid #6b7280;
  z-index: 10;
}
.mobile__nav {
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  column-gap: 2rem;
}
.mobile__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-top: 3px solid #94a3b8;
  height: 100%;
  width: 5.4rem;
  color: #94a3b8;
}
.mobile__link--active {
  color: #334155;
}
.mobile__link:nth-child(3) {
  border: none;
}
.mobile__icon {
  font-size: 2rem;
}
.mobile__icon--active {
  font-size: 3rem;
}
.mobile__text {
  font-size: 1rem;
  font-weight: 600;
  padding-top: 0.5rem;
}

/** ESCRITORIO STYLES NAVEGATION**/
.desktop__nav {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.desktop__list {
  display: flex;
  gap: 2rem;
  list-style: none;
}
.desktop__link {
  font-family: "Poppins", sans-serif;
  color: #0f172a;
  font-weight: 600;
  line-height: 2;
}
.desktop__link:hover {
  color: #1e40af;
}

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact__icon {
  font-size: 2rem;
  color: #ef4444;
  animation: floating 2s infinite ease-in-out;
}
.contact__text {
  font-size: 0.9rem;
  font-weight: 600;
  line-height: 1;
  padding-top: 0.3rem;
}

.button__dark-mode {
  height: fit-content;
  width: 3.5rem;
  padding: 0;
  border: none;
  background-color: transparent;
  font-size: 2.25rem;
}
.button__dark-mode:hover {
  cursor: pointer;
}
.button__dark-mode .fa-sun {
  color: #fbbf24;
}

/** ANIMACIONES **/
@keyframes shake {
  0% {
    transform: translate(0px, 0px) rotate(0deg);
  }
  25% {
    transform: translate(1px, 1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, -1px) rotate(-1deg);
  }
  75% {
    transform: translate(2px, -1px) rotate(2deg);
  }
  100% {
    transform: translate(-1px, 1px) rotate(-1deg);
  }
}
@keyframes floating {
  50% {
    transform: translateY(-4px);
  }
}
.main {
  padding-top: 8rem;
}
.main__container {
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 580px) {
  .main {
    padding-top: 10rem;
    padding-bottom: 4rem;
  }
}
@media (min-width: 768px) {
  .main {
    position: relative;
  }
  .main__container {
    height: 100%;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
    display: grid;
    gap: 2rem;
    grid-template-rows: repeat(3, 1fr);
    position: relative;
  }
}

@media (min-width: 768px) {
  .home {
    grid-column: 1/4;
    grid-row: 1/3;
  }
}
.home__text {
  margin-bottom: 2rem;
}
.home__heading {
  text-align: center;
  margin-bottom: 0;
  color: #1d4ed8;
  font-size: 3.5rem;
}
@media (min-width: 768px) {
  .home__heading {
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .home__heading {
    font-size: 4.5rem;
  }
}
.home__description, .home__text {
  font-weight: 600;
  line-height: 1.4;
  text-align: center;
}
@media (min-width: 768px) {
  .home__description, .home__text {
    text-align: left;
  }
}
.home__botones {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .home__botones {
    justify-content: flex-start;
  }
}

.words {
  margin: 1rem 0 2rem 0;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 2.6rem;
  color: #18181b;
}
@media (min-width: 768px) {
  .words {
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .words {
    font-size: 3.5rem;
  }
}
.words__text {
  color: #3b82f6;
}

.boton__linkedin {
  background-color: transparent;
  padding: 1rem;
  color: #0ea5e9;
  font-size: 1.5rem;
  display: inline-block;
  font-weight: 600;
  border: 1.5px solid #0ea5e9;
  border-radius: 0.6rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  width: auto;
}
@media (min-width: 768px) {
  .boton__linkedin {
    width: auto;
  }
}
.boton__linkedin:hover {
  background-color: #0ea5e9;
  color: #f8fafc;
  cursor: pointer;
}
.boton__github {
  background-color: transparent;
  padding: 1rem;
  color: #18181b;
  font-size: 1.5rem;
  display: inline-block;
  font-weight: 600;
  border: 1.5px solid #18181b;
  border-radius: 0.6rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  width: auto;
}
@media (min-width: 768px) {
  .boton__github {
    width: auto;
  }
}
.boton__github:hover {
  background-color: #18181b;
  color: #f8fafc;
  cursor: pointer;
}

.profile {
  width: 100%;
  height: 27.8rem;
}
@media (min-width: 768px) {
  .profile {
    margin: auto 0;
    grid-column: 4/6;
    grid-row: 1/3;
  }
}
.profile__box {
  position: relative;
  display: flex;
  justify-content: center;
}
.profile__box:hover .profile__back {
  transform: translateY(4%);
}
.profile__box:hover .profile__image {
  transform: translateX(-4%);
}
.profile__back, .profile__image {
  width: 25rem;
  position: absolute;
  transition-property: transform, left;
  transition-duration: 1s;
}
@media (min-width: 768px) {
  .profile__back, .profile__image {
    right: 0;
  }
}
@media (min-width: 768px) {
  .profile__image::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background: linear-gradient(to top, green 10%, red 25%);
    z-index: 10;
  }
}

@media (min-width: 768px) {
  .learning {
    grid-column: 1/3;
  }
}
.learning__heading {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .learning__heading {
    text-align: left;
  }
}
.learning__content {
  display: flex;
  justify-content: space-evenly;
  column-gap: 1rem;
}
@media (min-width: 768px) {
  .learning__content {
    justify-content: left;
    column-gap: 2rem;
  }
}
.learning__box {
  width: 7rem;
  height: 7rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
  background: linear-gradient(145deg, #e0e1e2, #ffffff);
  box-shadow: 9px 9px 18px #dbdcdd, -9px -9px 18px #ffffff;
}
@media (min-width: 480px) {
  .learning__box {
    height: 10rem;
    width: 10rem;
    border-radius: 1.5rem;
  }
}
.learning__image {
  width: 3rem;
  height: 3rem;
}
@media (min-width: 480px) {
  .learning__image {
    height: 5rem;
    width: 5rem;
  }
}
.learning__text {
  font-size: 1rem;
}

.concept {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .concept {
    grid-column: 3/6;
    padding-top: 1rem;
    justify-content: right;
  }
}
.concept__content {
  width: 100%;
  max-width: 50rem;
}
.concept__box {
  border: 1px solid #475569;
  border-radius: 10px;
  padding: 2rem;
  padding-left: 7rem;
  color: #64748b;
  line-height: 1.5;
  font-style: italic;
  font-size: 1.4rem;
  position: relative;
}
.concept__idea {
  margin: 0;
  margin-bottom: 1rem;
}
.concept__idea::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 3rem;
  height: 4rem;
  top: 2rem;
  left: 2rem;
  position: absolute;
}
.concept__author {
  font-weight: 700;
}

.skill {
  margin-top: 10rem;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-image: linear-gradient(to right, hsla(243deg, 58%, 21%, 0.6), hsla(168deg, 53%, 52%, 0.6)), url(../img/fondo-1.webp);
}
.skill .heading__text {
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 12rem;
}
.skill .heading__text::after {
  background-color: #1d4ed8;
}
.skill__container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 1fr);
  row-gap: 10rem;
  width: 92%;
  max-width: 30rem;
  margin: 0 auto;
}
@media (min-width: 520px) {
  .skill__container {
    max-width: 40rem;
  }
}
@media (min-width: 600px) {
  .skill__container {
    max-width: unset;
    column-gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (min-width: 900px) {
  .skill__container {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    max-width: 950px;
  }
}
.skill__card {
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(8px);
  border-radius: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-right: 2px solid rgba(255, 255, 255, 0.2);
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5.5rem 1rem 1rem 1rem;
  position: relative;
}
@media (min-width: 600px) {
  .skill__card:first-of-type {
    grid-column: 1/3;
  }
  .skill__card:nth-child(2) {
    grid-column: 3/5;
  }
  .skill__card:last-of-type {
    grid-column: 2/4;
  }
}
@media (min-width: 900px) {
  .skill__card:first-of-type {
    grid-column: initial;
  }
  .skill__card:nth-child(2) {
    grid-column: unset;
  }
  .skill__card:last-of-type {
    grid-column: initial;
  }
}
.skill__image {
  width: 12rem;
  position: absolute;
  top: -6rem;
  filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.8));
}
.skill__title {
  margin-top: 1rem;
  font-size: 1.7rem;
  font-weight: 600;
  text-align: center;
  color: #f3f4f6;
}
.skill__list {
  list-style: none;
  margin-top: 2.6rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
.skill__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 6.5rem;
  margin: 1rem;
}
.skill__icon {
  width: 4rem;
  height: 4rem;
}
.skill__icon--width {
  width: unset;
}
.skill__name {
  margin-top: 0.5rem;
  font-size: 1rem;
  color: #ffffff;
}
.skill__wave {
  margin-top: -1px;
}
.skill__wave--dark {
  display: none;
}
.skill__degraded {
  margin-bottom: -5px;
}
.skill__degraded--dark {
  display: none;
}

.project {
  margin-top: 10rem;
}

.loader {
  position: fixed;
  z-index: 10000;
  width: 100%;
  height: 100vh;
  background-color: #f9fafb;
  display: grid;
  justify-content: center;
  align-items: center;
}
.loader__custom {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background: radial-gradient(farthest-side, #1d4ed8 94%, rgba(0, 0, 0, 0)) top/8px 8px no-repeat, conic-gradient(rgba(0, 0, 0, 0) 30%, #1d4ed8);
  -webkit-mask: radial-gradient(farthest-side, rgba(0, 0, 0, 0) calc(100% - 8px), #000 0);
  animation: loading 1s infinite linear;
}

@keyframes loading {
  100% {
    transform: rotate(1turn);
  }
}
